<script>
import consultingStatus from '@/mixins/consultingStatus';

export default {
  name: 'Intro',
  props: {
    data: Object,
  },
  mixins: [consultingStatus],
  mounted() {
    this.$uiHandler.swiperNextPrevShow();

    if (this.data) {
      window.fromApp = this.data.fromApp;
    }
  },
  computed: {
    isKorean() {
      return this.$lang === this.$dictionary.languageType.KR;
    },
  },
  methods: {
    onClick(message, tag) {
      if (this.isOnConsulting) {
        return;
      }

      this.$analyticsHandler.apply(tag);
      this.$store.dispatch('SEND_MESSAGE', this.$t(message));
    },
  },
};
